<template>
    <div id="detail">
        <detail-nav-bar class="detail-nav" @titleClick='titleClick' ref='nav'/>
        <scroll class="content" ref="scroll" @scroll="contentScroll" :probe-type="3">
            <detail-swiper :top-images='topImages'></detail-swiper>
            <!-- <div>{{$store.state.cartList.length}}</div>
            <ul>
              <li v-for='item in $store.state.cartList'>{{item}}</li>
            </ul> -->
            <detail-base-info :goods="goods"/>
            <detail-shop-info :shop="shop"/>
            <detail-goods-info :detail-info="detailInfo" @imageLoad="imageLoad"/>
            <detail-param-info ref="param" :param-info="paramInfo"/>
            <detail-comment-info ref="comment" :comment-info="commentInfo"></detail-comment-info>
            <good-list :goods="recommendList"  ref="recommend"></good-list>
        </scroll>
        <detail-bottom-bar @addToCart='addToCart'></detail-bottom-bar>
        <back-top @click.native="backClick" v-show="isShowBackTop"/>
<!-- <toast :msg='msg' :show='show'></toast> -->

    </div>
</template>
<script>
import DetailNavBar from './childComps/DetailNavBar'
import DetailSwiper from './childComps/DetailSwiper'
import DetailBaseInfo from './childComps/DetailBaseInfo'
 import DetailShopInfo from './childComps/DetailShopInfo'
import DetailGoodsInfo from './childComps/DetailGoodsInfo'
import DetailParamInfo from './childComps/DetailParamInfo'
import DetailCommentInfo from './childComps/DetailCommentInfo'
import GoodList from 'components/content/goods/GoodList'
import DetailBottomBar from './childComps/DetailBottomBar'


import Scroll from 'components/common/scroll/Scroll'

import {getDetail,Goods,Shop,GoodsParam,getRecommend} from 'network/detail'
import {debounce} from 'common/utils.js'
import {itemListenerMixin,backTopMixin} from 'common/mixin'

import {mapActions} from 'vuex'

import Toast from 'components/common/toast/Toast'
export default {
    name:'Detail',
    components:{
        DetailNavBar,
        DetailSwiper,
        DetailBaseInfo,
        DetailShopInfo,
        Scroll,
        DetailGoodsInfo,
        DetailParamInfo,
        DetailCommentInfo,
        GoodList,
        DetailBottomBar,
        Toast
        

    },
    data () {
        return {
            iid:null,
            topImages: [],
            goods:{},
            shop: {},
            detailInfo:{},
            paramInfo: {},
            commentInfo: {},
            recommendList: [],
            themeTopYs: [],
            getThemeTopY:null,
            currentIndex: 0,
            msg:'',
            show:false
            

        }
    },
    mixins:[itemListenerMixin,backTopMixin],
    created() {
         // 1.iid
        this.iid=this.$route.query.iid;
        // 2.iid
        getDetail(this.iid).then(res=>{
            // console.log(res)
            const data=res.result;
             // 1.
            this.topImages=data.itemInfo.topImages
            //2.
            this.goods=new Goods(data.itemInfo, data.columns, data.shopInfo.services)
            //3.
            this.shop = new Shop(data.shopInfo)
            //4.
            this.detailInfo = data.detailInfo;
            // 5.
            this.paramInfo = new GoodsParam(data.itemParams.info, data.itemParams.rule)
            //6.
            if (data.rate.list) {
            this.commentInfo = data.rate.list[0];
              //
              //this.$refs.param.$elthis.$refs.recommend.$el
                // this.themeTopYs=[]
                // this.themeTopYs.push(0);
                // this.themeTopYs.push(this.$refs.param.$el.offsetTop)
                // this.themeTopYs.push(this.$refs.comment.$el.offsetTop)
                // this.themeTopYs.push(this.$refs.recommend.$el.offsetTop)
                // console.log(this.themeTopYs)
            //2
            //
            //DOM
            //offsetTop
            //offsetTop
              // this.$nextTick(()=>{
              //   this.themeTopYs=[]
              //   this.themeTopYs.push(0);
              //   this.themeTopYs.push(this.$refs.param.$el.offsetTop)
              //   this.themeTopYs.push(this.$refs.comment.$el.offsetTop)
              //   this.themeTopYs.push(this.$refs.recommend.$el.offsetTop)
              //   console.log(this.themeTopYs)
              // })            
          }
        })
        //3.
        getRecommend().then(res=>{
            // console.log(res)
            this.recommendList = res.data.list

        })
        //4.getThemeTopY
        this.getThemeTopY=debounce(()=>{
          this.themeTopYs=[]
          this.themeTopYs.push(0);
          this.themeTopYs.push(this.$refs.param.$el.offsetTop)
          this.themeTopYs.push(this.$refs.comment.$el.offsetTop)
          this.themeTopYs.push(this.$refs.recommend.$el.offsetTop)
          //2
          this.themeTopYs.push(Number.MAX_VALUE)
          console.log(this.themeTopYs) 
        },500)

    },
    mounted() {
      // const refresh=debounce(this.$refs.scroll.refresh,500)
      // this.itemImgListener=()=>{
      //     refresh(); 
      // }
      // this.$bus.$on('itemImageLoad',this.itemImgListener)

    },
    updated() {


    },
    methods: {
      ...mapActions(['addCart']),
        imageLoad(){
          //
          //  this.$refs.scroll.refresh()
          //
          this.refresh() 
          //
          this.getThemeTopY()
        },
        titleClick(index){
          // console.log(index);
          // console.log(this.themeTopYs[index])
          this.$refs.scroll.scrollTo(0,-this.themeTopYs[index],100)
        },
        contentScroll(position){
          // console.log(position)
          //1.y
          const positionY=-position.y
          // console.log(positionY)
          //2.positionY[0, 6619, 7380, 7691]
          //positionY06619 index=0
          //positionY66197380 index=1
          // for(let i in this.themeTopYs){
            // console.log(i)  
            //i+1   i*1
            //i+1 undefined 
            let length=this.themeTopYs.length;
            // for(var i=0;i<length;i++)
            //
            for(var i=0;i<length-1;i++){
            // if(positionY>this.themeTopYs[i]&&positionY<this.themeTopYs[i+1]){
            //   console.log(i)
            // }
            //
            // if(this.currentIndex !== i && ((i<length-1 && positionY>=this.themeTopYs[i] && positionY<this.themeTopYs[i+1]) || (i===length-1 && positionY>=this.themeTopYs[i]))){
            //   this.currentIndex=i;
            //     console.log(this.currentIndex)
            //     this.$refs.nav.currentIndex=this.currentIndex
            // }
            //
            //themeTopYs
            //jsNumber.MAX_VALUE

            if(this.currentIndex !== i && (positionY>=this.themeTopYs[i] && positionY<this.themeTopYs[i+1])){
              this.currentIndex=i;
                console.log(this.currentIndex)
                this.$refs.nav.currentIndex=this.currentIndex
            }
          }
            //3.
            // this.isShowBackTop=(-position.y)>1000 
            this.listenShowBackTop(position)
        },
        addToCart(){
          // console.log('')
          //
          // 1.
          const product={}
          // 2.
          product.image=this.topImages[0];
          product.title=this.goods.title;
          product.desc=this.goods.desc;
          product.price=this.goods.realPrice;
          product.iid=this.iid;
          // 3.Store
          // this.$store.commit('addCart',product)
          //mapactions
          // this.$store.dispatch('addCart',product).then(res=>{
          //   console.log(res)
          // })
          //
          this.addCart(product).then(res=>{
            console.log(res)
          //   this.show=true;
          //   this.msg=res
          //   setTimeout(() => {
          //     this.show=false;
          //     this.msg=''
          //   }, 2000);
          this.$toast.show(res,2000)
          })
          


        },




    },
    destroyed() {//keep-alive
        //1.        
        this.$bus.$off('itemImageLoad',this.itemImgListener)
        
    },  
 
    
}
</script>
<style scoped>
  #detail {
    position: relative;
    z-index: 9;
    background-color: #fff;
    height: 100vh;
  }

  .detail-nav {
    position: relative;
    z-index: 9;
    background-color: #fff;
  }

  .content {
    /* height: calc(100% - 44px -58px); */
    height: calc(100% - 102px);
  }
</style>


